import React from 'react'
import beornings from '../images/race_beorning.jpg'

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-4">
                <img className="race-profile" src={beornings} />
            </div>
            <div className="col-lg-8">
                <h1>Beornings</h1>
                <p><strong>Beornings</strong> are an ancient immortal race that lives in Middle-Earth. </p>
            </div>
        </div>
    </div>
</div>


